* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #fafafa;
}

.container {
  max-width: 960px;
  margin: 0 auto;
}

header {
  height: 90px;
  background: #ffffff;
  box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.05);
}

.logo {
  display: block;
}

.logo img {
  width: 50px;
  -webkit-filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.25));
  filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.25));
}

.h-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.menu ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu ul li {
  margin: 0 20px;
}

.menu-links {
  font-size: 18px;
  color: #000000;
  text-decoration: none;
}

.feed {
  display: flex;
  justify-content: space-between;
  margin-top: 70px;
}

.left {
  max-width: 850px;
}

.post {
  margin-bottom: 70px;
  width: 650px;
  min-height: 560px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
}

.post .user {
  margin: 12px 20px;
}

.post > img {
  width: 100%;
  height: 420px;
  object-fit: cover;
}

.post-name {
  padding: 10px 20px;
  font-weight: 500;
  font-size: 18px;
}

.post-descr {
  padding: 0 20px;
  margin: 0px 0 20px 0;
}

.user {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-decoration: none;
  color: #000;
}

.user img {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  object-fit: cover;
  border: 1px solid rgb(201, 46, 140);
}

.user div {
  margin-left: 20px;
  font-size: 18px;
  font-weight: 500;
}

.min {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 10px 0 15px 10px;
}

.min img {
  width: 45px;
  height: 45px;
  border-radius: 100%;
  object-fit: cover;
}

.min div {
  margin-left: 10px;
  font-size: 14px;
  font-weight: 400;
}

.users-block {
  margin-top: 20px;
  padding: 20px;
  width: 265px;
  min-height: 260px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
}

.profile {
  margin-top: 70px;
  margin-bottom: 70px;
}

.pallete {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
}

.pallete img {
  width: calc(100% / 3 - 6px);
  margin: 3px;
  height: 300px;
  object-fit: cover;
}

.profile-user {
  margin-bottom: 70px;
}
.profile-user img {
  width: 100px;
  height: 100px;
}
